.page {
  font-family: 'Manrope', sans-serif;
  color: $main-text-color;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    pointer-events: none;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }

  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }
}





